@import 'variables.scss';

#unsupported-dapps {
    .content {
        display: flex;
        flex-direction: column;
        gap: 1em;

        .separator {
            flex-grow: 1;
        }

        .dapps-list {
            display: flex;
            flex-direction: column;
            gap: 1em;

            .dapp {
                display: flex;
                align-items: center;
                gap: 0.5em;
                font-size: 18px;
                font-weight: 500;

                .icon {
                    @include flex-center-center;
                    height: 28px;
                    width: 28px;
                    border-radius: 100%;
                    background-size: contain;
                    background-color: rgba(0, 0, 0, 0.2);

                    svg {
                        height: 14px;
                        opacity: 0.3;
                    }
                }
            }
        }

        .advanced-mode {
            .advanced-mode-label {
                font-weight: bold;
            }
        }
    }
}